<template>
	<f7-page hide-toolbar-on-scroll>
		<Navbar></Navbar>
		<InfoPage></InfoPage>
		<div class="container">
			<form action="post" id="form-rate">
				<f7-row class="message-box">
					<p>Cm n bn  gi ticket cho SohaGame.<br>Vui lng nh gi cht lng h tr cho chng ti bng cch chn s sao di y</p>
					<div class="row rate-box" >
						<span v-for="(star, index) in stars" :key="index" :id="star.star_id" class="star" v-on:click="rate(star.star_id)" @mouseover="hover(this)" @mouseleave="hoverOut(this)" :class="{'hover-active':isHover > index, 'active':isActive > index}"></span>
						<input type="hidden" name="star-rate" :value="starValue">
					</div>
				</f7-row>

				<f7-row class="comment-box-2" v-bind:style="{display: rateVeryLow}">
					<div class="comment-title">Khng hi lng</div>
					<div class="comment-message">
						Cm n bn  gi nh gi. Chc bn chi game vui v. Bn c th  li s in thoi ti b phn h tr ca chng ti:
					</div>
					<div class="comment-checked">
						<div class="comment-textbox row">
						<textarea  name="tel_number" id="" placeholder="  li s in thoi" cols="80" rows="2"></textarea>
					</div>
					</div>
				</f7-row>

				<f7-row class="comment-box-2" v-bind:style="{display: rateLow}">
					<div class="comment-title">Bnh thng</div>
					<div class="comment-message">
						Cm n bn  gi nh gi. Chc bn chi game vui v. Bn c th gi  kin ti b phn h tr ca chng ti:
					</div>
					<div class="comment-checked">
						<div class="feedback-checked">
							<input type="checkbox" id="feedback-time-late"><lable for="feedback-time-late">Thi gian phn hi mun</lable>
						</div>
						<div class="feedback-checked">
							<input type="checkbox" id="feedback-bad"><lable for="feedback-bad">Thi  khng tt</lable>
						</div>
						<div class="feedback-checked">
							<input type="checkbox" id="feedback-other" v-on:click="otherFeedback"><lable for="feedback-other"> kin khc</lable>
						</div>
						<div class="comment-textbox row" v-bind:style="{display:checkedOther}">
							<textarea  name="other_feedback" id="" placeholder="  li  kin" cols="80" rows="2"></textarea>
						</div>
					</div>
				</f7-row>

				<f7-row class="comment-box-1" v-bind:style="{display: rateHight}">
					<div class="comment-title">Rt hi lng</div>
					<div class="comment-message">
						Cm n bn  gi nh gi. Chc bn chi game vui v. Bn c th gi li khen ti b phn h tr ca chng ti:
					</div>
					<div class="comment-textbox row">
						<textarea  name="good_job" id="" placeholder=" li li khen" cols="80" rows="2"></textarea>
					</div>
				</f7-row>

				<button class="btn-send"><p>Gi</p></button>
			</form>
		</div>
		<!-- footer -->
        <f7-toolbar bottom-md>
            <div class="tel-vn">
                Tel : 1900 6639 (Vit Nam)
            </div>
            <div class="tel-qt">
                +84-24-73 09 6868
            </div>
        </f7-toolbar>
	</f7-page>
</template>
<script>
import Navbar from './navbar.vue';
import InfoPage from './info.vue';

export default {
    data()  {
        return {
			isActive: false,
			isHover: false,
			rateLow: 'none',
			rateHight: 'none',
			rateVeryLow: 'none',
			checkedOther: 'none',
			starValue: 0,
			hover: function(){
				var star_id = event.target.id;
				var max_star = star_id.substring(5);
				this.isHover = max_star;
			},
			hoverOut: function(){
				this.isHover = false;
			},
			stars: [
				{star_id: 'star-1'},
				{star_id: 'star-2'},
				{star_id: 'star-3'},
				{star_id: 'star-4'},
				{star_id: 'star-5'}
			]
        }
    },
    components : {
		Navbar,
		InfoPage
    },
    methods: {
		rate: function (star_id) {
			var star = star_id.substring(5);
			// this.isActive = false;
			this.isActive = star;
			this.starValue = star;
			if(star == 5){
				this.rateVeryLow = 'none';
				this.rateLow = 'none';
				this.rateHight = 'block';
			}
			else if (star == 3 |star == 4) {
				this.rateLow = 'block';
				this.rateHight = 'none';
				this.rateVeryLow = 'none';
			}else{
				this.rateLow = 'none';
				this.rateHight = 'none';
				this.rateVeryLow = 'block';
			}
		},
		otherFeedback: function(){
			var checked = event.target.checked;
			if (checked) {
				this.checkedOther = 'block'
			}else{
				this.checkedOther = 'none'
			}
		}
	},
}

</script>

<style scoped>
	body{
		background-color: #f5f6f8;
	}
	
</style>